function buildDistanceInWordsLocale () {
  var distanceInWordsLocale = {
    lessThanXSeconds: {
      one: 'kevesebb, mint egy msodperce',
      other: 'kevesebb, mint {{count}} msodperce'
    },

    xSeconds: {
      one: '1 msodperce',
      other: '{{count}} msodperce'
    },

    halfAMinute: 'fl perce',

    lessThanXMinutes: {
      one: 'kevesebb, mint egy perce',
      other: 'kevesebb, mint {{count}} perce'
    },

    xMinutes: {
      one: '1 perce',
      other: '{{count}} perce'
    },

    aboutXHours: {
      one: 'kzel 1 rja',
      other: 'kzel {{count}} rja'
    },

    xHours: {
      one: '1 rja',
      other: '{{count}} rja'
    },

    xDays: {
      one: '1 napja',
      other: '{{count}} napja'
    },

    aboutXMonths: {
      one: 'kzel 1 hnapja',
      other: 'kzel {{count}} hnapja'
    },

    xMonths: {
      one: '1 hnapja',
      other: '{{count}} hnapja'
    },

    aboutXYears: {
      one: 'kzel 1 ve',
      other: 'kzel {{count}} ve'
    },

    xYears: {
      one: '1 ve',
      other: '{{count}} ve'
    },

    overXYears: {
      one: 'tbb, mint 1 ve',
      other: 'tbb, mint {{count}} ve'
    },

    almostXYears: {
      one: 'majdnem 1 ve',
      other: 'majdnem {{count}} ve'
    }
  }

  function localize (token, count, options) {
    options = options || {}

    var result
    if (typeof distanceInWordsLocale[token] === 'string') {
      result = distanceInWordsLocale[token]
    } else if (count === 1) {
      result = distanceInWordsLocale[token].one
    } else {
      result = distanceInWordsLocale[token].other.replace('{{count}}', count)
    }

    if (options.addSuffix) {
      if (options.comparison > 0) {
        return '' + result
      } else {
        return result + ''
      }
    }

    return result
  }

  return {
    localize: localize
  }
}

module.exports = buildDistanceInWordsLocale
